'use server'

import { getServerSession } from '@/lib/supabase/auth-helpers'
import { userProfileQueries } from '@/lib/db/queries'
import { userProfileSchema, validateWithZod } from '@/lib/validations/schemas'
import type { FundingRequirements } from '@/lib/db-schema'
import type { UserProfileResponse } from './types'

/**
 * Input type for creating/updating user profile
 * @deprecated Use UserProfileInput from lib/validations/schemas instead
 * Kept for backward compatibility
 */
export interface UserProfileInput {
  company_name: string
  registration_number: string
  industry: string
  business_description: string
  annual_revenue: number
  employees_count: number
  years_in_business: number
  location: string
  funding_requirements: FundingRequirements
}

// Re-export for convenience
export type { UserProfileInput as UserProfileInputType } from '@/lib/validations/schemas'
export type { UserProfileResponse }

/**
 * Get user's profile from database
 */
export async function getUserProfile(): Promise<UserProfileResponse> {
  try {
    // Check authentication
    const user = await getServerSession()
    if (!user) {
      return { success: false, error: 'Authentication required. Please log in.' }
    }

    // Use optimized query helper
    const result = await userProfileQueries.getByUserId(user.id)
    return result
  } catch (error: any) {
    // Check if this is a Next.js redirect error - if so, re-throw it
    if (error && typeof error === 'object' && 'digest' in error && error.digest?.startsWith('NEXT_REDIRECT')) {
      throw error
    }
    console.error('Error in getUserProfile:', error)
    return { success: false, error: error.message || 'Failed to fetch profile' }
  }
}

/**
 * Create a new user profile
 */
export async function createUserProfile(input: UserProfileInput): Promise<UserProfileResponse> {
  try {
    // Validate input with Zod
    const validation = validateWithZod(userProfileSchema, input)
    if (!validation.success) {
      return { success: false, error: validation.error }
    }

    // Check authentication
    const user = await getServerSession()
    if (!user) {
      return { success: false, error: 'Authentication required. Please log in.' }
    }

    // Use optimized query helper
    const result = await userProfileQueries.upsert(user.id, validation.data)
    return result
  } catch (error: any) {
    // Check if this is a Next.js redirect error - if so, re-throw it
    if (error && typeof error === 'object' && 'digest' in error && error.digest?.startsWith('NEXT_REDIRECT')) {
      throw error
    }
    console.error('Error in createUserProfile:', error)
    return { success: false, error: error.message || 'Failed to create profile' }
  }
}

/**
 * Update existing user profile
 */
export async function updateUserProfile(input: Partial<UserProfileInput>): Promise<UserProfileResponse> {
  try {
    // Validate input with Zod (partial schema)
    const { userProfileUpdateSchema } = await import('@/lib/validations/schemas')
    const validation = validateWithZod(userProfileUpdateSchema, input)
    if (!validation.success) {
      return { success: false, error: validation.error }
    }

    // Check authentication
    const user = await getServerSession()
    if (!user) {
      return { success: false, error: 'Authentication required. Please log in.' }
    }

    // Check if profile exists
    const existingResult = await userProfileQueries.getByUserId(user.id)
    
    if (existingResult.success && existingResult.data) {
      // Update existing profile
      const result = await userProfileQueries.update(user.id, validation.data)
      return result
    } else {
      // Create new profile if doesn't exist (upsert behavior)
      const result = await userProfileQueries.upsert(user.id, validation.data)
      return result
    }
  } catch (error: any) {
    // Check if this is a Next.js redirect error - if so, re-throw it
    if (error && typeof error === 'object' && 'digest' in error && error.digest?.startsWith('NEXT_REDIRECT')) {
      throw error
    }
    console.error('Error in updateUserProfile:', error)
    return { success: false, error: error.message || 'Failed to update profile' }
  }
}

/**
 * Upsert user profile (create or update in one operation)
 * This is the recommended method as it handles both cases automatically
 */
export async function upsertUserProfile(input: UserProfileInput): Promise<UserProfileResponse> {
  try {
    // Validate input with Zod
    const validation = validateWithZod(userProfileSchema, input)
    if (!validation.success) {
      return { success: false, error: validation.error }
    }

    // Check authentication
    const user = await getServerSession()
    if (!user) {
      return { success: false, error: 'Authentication required. Please log in.' }
    }

    // Use optimized query helper
    const result = await userProfileQueries.upsert(user.id, validation.data)
    return result
  } catch (error: any) {
    // Check if this is a Next.js redirect error - if so, re-throw it
    if (error && typeof error === 'object' && 'digest' in error && error.digest?.startsWith('NEXT_REDIRECT')) {
      throw error
    }
    console.error('Error in upsertUserProfile:', error)
    return { success: false, error: error.message || 'Failed to save profile' }
  }
}

/**
 * Check if user profile is complete (has all required onboarding data)
 * Returns true if all required fields are filled, false otherwise
 */
export async function isProfileComplete(): Promise<boolean> {
  try {
    // Use getServerSession instead of requireAuth to avoid redirect issues
    const user = await getServerSession()
    if (!user) {
      return false
    }
    const supabase = await createClient()

    const { data: profile, error } = await supabase
      .from('user_profiles')
      .select('*')
      .eq('user_id', user.id)
      .maybeSingle()

    // If no profile exists or error occurred, profile is incomplete
    if (error || !profile) {
      return false
    }

    // Check all required fields are filled
    // Basic business information
    if (!profile.company_name || profile.company_name.trim() === '') return false
    if (!profile.registration_number || profile.registration_number.trim() === '') return false
    if (!profile.industry || profile.industry.trim() === '') return false
    if (!profile.business_description || profile.business_description.trim() === '') return false
    if (!profile.location || profile.location.trim() === '') return false

    // Business metrics
    if (profile.annual_revenue === null || profile.annual_revenue === undefined) return false
    if (profile.employees_count === null || profile.employees_count === undefined) return false
    if (profile.years_in_business === null || profile.years_in_business === undefined) return false

    // Funding requirements (JSONB field)
    if (!profile.funding_requirements || typeof profile.funding_requirements !== 'object') return false
    
    const fundingReqs = profile.funding_requirements as any
    if (!fundingReqs.amount_needed || fundingReqs.amount_needed === null || fundingReqs.amount_needed === undefined) return false
    if (!fundingReqs.funding_purpose || fundingReqs.funding_purpose.trim() === '') return false

    // All required fields are present and filled
    return true
  } catch (error: any) {
    console.error('Error checking profile completion:', error)
    // On error, assume incomplete to be safe
    return false
  }
}

